import React from "react";
import styles from "./Solution.module.css";
import parse from "html-react-parser";
import { optionSelectStaticMathField } from "../component/HorizontalFillUpsEquationType/replaceDomeNode/ReplaceDomNode";
import CkEditorAnswer from "../component/Ckeditor/CkEditorAnswer";
import OrcAnswered from "../component/ORC/OrcAnswered";
import OprcAnswered from "../component/Oprc/OprcAnswered";
import MainOl from "../component/OL/MainOl";
import HtmlParser from "react-html-parser";
import {
  addLazyLoading,
  removeUnwantedTags,
} from "../CommonJSFiles/gettingResponse";
export default function ExplanationBoxContainer({ questionData, temp }) {
  return (
    <>
      {questionData.operation ? (
        <NewTypeSolution solution={temp?.solution || {}} />
      ) : questionData?.solution ? (
        <NewTypeSolution
          solution={questionData?.solution || {}}
          className={"old_type"}
        />
      ) : (
        <OldTypeSolution
          model={questionData?.choice_data}
          type={questionData.question_type}
          obj={questionData}
        />
      )}
    </>
  );
}

function NewTypeSolution({ solution, className }) {
  let model = solution["model"] || solution[".model"] || [];
  return model?.length ? (
    <>
      <p className={styles.explanation}>Explanation: </p>
      <div
        className={`${styles.explanationBoxContainer} ${
          className === "old_type" ? "" : styles.word_wrap
        } ${className === "old_type" ? "" : styles.word_wrap_space}`}
      >
        <div
          style={{
            fontSize: 20,
            fontWeight: "bold",
            opacity: 0.8,
            color: "#666",
          }}
        >
          {model?.map((item, index) => {
            var questionTextFormatted = removeUnwantedTags(item?.val);
            questionTextFormatted = addLazyLoading(questionTextFormatted);
            console.log("questionTextFormatted", { questionTextFormatted });
            return (
              <div key={index}>
                {parse(
                  questionTextFormatted ?? "",
                  optionSelectStaticMathField
                )}
              </div>
            );
          })}
        </div>
      </div>
    </>
  ) : (
    ""
  );
}

function OldTypeSolution({ model, type, obj }) {
  let oldType = {
    ckeditor: (
      <CkEditorAnswer
        str={obj?.question_text}
        choiceData={obj?.choice_data}
        hideSolutionText={true}
      />
    ),
    orc: <OrcAnswered obj={obj} question_text={obj?.question_text} />,
    ol: <MainOl obj2={{ question_data: [{ ...obj }] }} answer={true} />,
    oprc: <OprcAnswered obj={obj} />,
  };
  if (oldType.hasOwnProperty(type))
    return (
      <>
        <p className={styles.explanation}>Explanation: </p>
        <div className={styles.explanationBoxContainer}>
          <div
            style={{
              fontWeight: "initial",
              fontSize: "initial",
              lineHeight: "initial",
            }}
          >
            {oldType[type]}
          </div>
        </div>
      </>
    );
  return (
    <>
      {model?.length ? (
        <>
          <p className={styles.explanation}>Explanation: </p>
          <div className={styles.explanationBoxContainer}>
            <div
              style={{
                fontWeight: "initial",
                fontSize: "initial",
                lineHeight: "initial",
              }}
            >
              {model?.map(
                (item, i) =>
                  (item.correct ||
                    String(type).trim() == "Fill in the blanks ".trim()) && (
                    <div key={i}>
                      <div>{HtmlParser(item.choices)}</div>
                      {item?.solution && (
                        <div>{HtmlParser(item?.solution)}</div>
                      )}
                      {item?.solution_image && (
                        <div>{<img src={item?.solution_image} />}</div>
                      )}
                      {item?.solution1 && (
                        <div>{HtmlParser(item?.solution1)}</div>
                      )}
                      {item?.solution1_image && (
                        <div>{<img src={item?.solution1_image} />}</div>
                      )}
                      {item?.solution2 && (
                        <div>{HtmlParser(item?.solution2)}</div>
                      )}
                      {item?.solution2_image && (
                        <div>{<img src={item?.solution2_image} />}</div>
                      )}
                    </div>
                  )
              )}
            </div>
          </div>
        </>
      ) : (
        ""
      )}
    </>
  );
}
