export default function compareLatexData(val1, val2) {
  var IntegerPart = val1?.split("\\")[0];
  const numeDenomArr = [...val1.matchAll(/\{([^}]*)\}/g)].map(
    (match) => match[1]
  );
  // console.log({ IntegerPart, numeDenomArr });

  var impureFracVal;
  var mixedfracVal;
  if (IntegerPart) {
    impureFracVal = `\\frac{${
      Number(IntegerPart * numeDenomArr[1]) + Number(numeDenomArr[0])
    }}{${Number(numeDenomArr[1])}}`;
    mixedfracVal = val1;
  } else {
    mixedfracVal = `${
      Math.floor(Number(numeDenomArr[0]) / Number(numeDenomArr[1])) > 0
        ? Math.floor(Number(numeDenomArr[0]) / Number(numeDenomArr[1]))
        : ""
    }\\frac{${Number(numeDenomArr[0]) % Number(numeDenomArr[1])}}{${Number(
      numeDenomArr[1]
    )}}`;
    // impureFracVal = `\\frac{${numeDenomArr[0]}}{${numeDenomArr[1]}}`;
    impureFracVal = val1;
  }
  console.log("{impureFracVal,mixedfracVal}", { impureFracVal, mixedfracVal });

  let str = "\\";
  val1 = String(val1);
  val2 = String(val2);
  val1 = val1.split(str);
  val2 = val2.split(str?.trim());
  //trim from begginning
  while (val1.length) {
    if (val1[0] === str || val1[0]?.trim() == "") val1.shift();
    else {
      break;
    }
  }

  while (val2.length) {
    if (val2[0] === str || val2[0]?.trim() == "") val2.shift();
    else {
      break;
    }
  }
  //trim from ending
  while (val1.length) {
    if (val1[val1.length - 1] === str || val1[val1.length - 1]?.trim() == "")
      val1.pop();
    else {
      break;
    }
  }

  while (val2.length) {
    if (val2[val2.length - 1] === str || val2[val2.length - 1]?.trim() == "")
      val2.pop();
    else {
      break;
    }
  }
  if (impureFracVal[0] == "\\")
    impureFracVal = impureFracVal.replaceAll("\\", "");
  if (mixedfracVal[0] == "\\") mixedfracVal = mixedfracVal.replaceAll("\\", "");

  // return val1.join(str)?.trim() === val2.join(str)?.trim();
  console.log("val2.join(str)?.trim() ", val2.join(str)?.trim());
  console.log("impureFracVal", impureFracVal, impureFracVal[0] == "\\");
  console.log("mixedfracVal", mixedfracVal, mixedfracVal[0] == "\\");

  console.log(
    "val2.join(str)?.trim() === impureFracVal",
    val2.join(str)?.trim() === impureFracVal,
    "val2.join(str)?.trim() === mixedfracVal",
    val2.join(str)?.trim() === mixedfracVal
  );
  return (
    val2.join(str)?.trim() === impureFracVal ||
    val2.join(str)?.trim() === mixedfracVal
  );
}
