import React from "react";
import { useState } from "react";
import { useEffect } from "react";
import HtmlParser from "html-react-parser";
import { optionSelectStaticMathField } from "../component/HorizontalFillUpsEquationType/replaceDomeNode/ReplaceDomNode";
import { Pattern } from "../component/questiontextoptions/clickableOnPicture/pattern";
import styles from "./Solution.module.css";
import styled from "styled-components";
import { CorrectLongDvision2 } from "../component/LongDivision/MainLongDivision";
import { MainVerticalCorrectAnswer } from "../component/Vertical/MainVertical";

export default function CondiitonalCorrectAnswer({ questionData, temp }) {
  console.log('this is questiondata',questionData)
  let answerValue =
    (temp?.answer && temp?.answer !== "true" ? temp?.answer : false) ||
    (temp?.answerValue &&
    temp.answerValue !== true &&
    temp.answerValue !== "true"
      ? temp.answerValue
      : false) ||
    temp?.answerCount;
  console.log(answerValue);

  let oldType = ["Multiple choice", "True/False"];
  if (questionData?.question_type == "questiontextoptions") {
    return (
      <div className={styles.correctAnswerBox}>
        <div className={`${styles.correctAnswer} ${styles.correctAnswer2}`}>
          <h6>The correct answer is:</h6>
          <QuestionTextOptionAnswerContainer
            temp={temp?.questionContent[0] || {}}
          />
        </div>
      </div>
    );
  }
  return oldType.includes(questionData?.question_type) ? (
    <MultipleChoiceQuestionCorrectAnswer
      choices={
        questionData?.choice_data?.filter((item) => item.correct)[0] || {}
      }
    />
  ) : questionData?.operation ? (
    answerValue ? (
      <div className={styles.correctAnswerBox}>
        <div className={`${styles.correctAnswer} ${styles.correctAnswer2}`}>
          <h6>The correct answer is:</h6>
          <h6>{answerValue}</h6>
        </div>
      </div>
    ) : temp?.choiceType == "selectchoice" ||
      (temp?.type === "options_multiple_pictures" ||
      temp?.type ==="options_select_multiple_pictures" )? (
      <MultipleChoiceCorrectAnswer data={temp} choiceType={temp?.type} />
    ) : temp?.type === "short_division" ? (
      <ShortDivisionCorrectAnswer data={temp} />
 
    ):temp?.type==="long_division"?(
      <CorrectLongDvision2 data={temp}/>
    ):(temp?.type==="long_multiplication" || temp?.type==="vertical" )?(
      <MainVerticalCorrectAnswer data={temp}/>
    ):(
      ''
    )
  ) : (
    ""
  );
}
function HtmlAndMathQuillParser(data) {
 
  let str = String(data);
  return str.includes("mq-selectable")
    ? HtmlParser(data, optionSelectStaticMathField)
    : HtmlParser(data);
}

function MultipleChoiceCorrectAnswer({ data, choiceType }) {


  const [choiceData, setChoiceData] = useState([]);
  console.log('this is chociedata',choiceData)
  const getCorrectValue = (obj, temp) => {
    if (!Array.isArray(obj) && typeof obj === "object") {
      if (
        obj?.selected == "true" ||
        obj.isMissed == "true" ||
        obj?.option == "true"
      ) {
        temp.push({ ...obj });

        if (choiceType === "options_select_multiple_pictures") {
          return false;
        } else {
          return true;
        }
      } else return false;
    }
    if (Array.isArray(obj))
      for (let item of obj) {
        if (getCorrectValue(item, temp)) return;
      }
    return false;
  };
  
  useEffect(() => {
    let temp = [];
    let questionContent = data?.questionContent || [];
    console.log('this is questioncontent',questionContent);
    console.log('this is choices',data?.choices);
    if (!Array.isArray(questionContent)) questionContent = data?.choices;
    getCorrectValue(questionContent, temp);
    setChoiceData([...temp]);
   
  }, []);

  return choiceData?.length ? (
 <>
{choiceType === "options_select_multiple_pictures" ? (
  <>  
    <div className={styles.correctAnswerBox}>
    <div className={`${styles.correctAnswer} ${styles.correctAnswer2}`}>
      <h6>The correct answer is:</h6>
    </div>
    <br />
    <div style={{
      color: "#233584",
      display: "grid",
      width: "90%",
      marginTop: "1rem",
      gap: "1rem",
      position: "relative",
      gridTemplateColumns: "repeat(2,1fr)"
    }}>
      {choiceData?.map((item, key) => (
        <div key={key}
          style={{
            gap: "2rem",
            backgroundColor:"#b9c2fc",
            display: "flex",
            cursor: "pointer",
            border: " 1px solid black",
            padding: "1rem",
            borderRadius: "5px",
            fontWeight: "bold",
            alignItems: "center",
          }}
        >
          <p  style={{margin:'0px'}} className="mathzone-circle-selectbox">{['A', 'B', 'C', 'D', 'E', 'F'][item?.col - 1]}</p>
          <div  style={{display: "flex", flexWrap: "wrap", gap: "4px"}}>
          {HtmlAndMathQuillParser(
            item?.value || item?.numvalue || item.image
          )}
          </div>
        </div>
      ))}
    </div>
   </div>
  </>
) : (
  choiceType === "options_multiple_pictures" ? (
    <>
    <div className={styles.correctAnswerBox}>
      <div className={`${styles.correctAnswer} ${styles.correctAnswer2}`}>
        <h6>The correct answer is:</h6>
        {choiceData?.map((item, key) => (
          <h6 key={key}>
            {HtmlAndMathQuillParser(
              item?.value || item?.numvalue || item.image
            )}
            {key < choiceData.length - 1 && " , "}
          </h6>
        ))}
      </div>
    </div>
    </>
  ) : (
    ""
  )
)}
</>
  ) : (
    ""
  );
}

function MultipleChoiceQuestionCorrectAnswer({ choices }) {
  console.log(choices);
  return (
    <div className={styles.correctAnswerBox}>
      <div className={styles.correctAnswer}>
        <h6>The correct answer is:</h6>
        <div>
          {choices.choices && <div>{HtmlParser(choices.choices)}</div>}
          {choices.choice_image && (
            <div>
              <img
                src={choices.choice_image}
                style={{ maxWidth: "100%", height: "fit-contain" }}
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
}
function QuestionTextOptionAnswerContainer({ temp }) {
  let data = temp?.filter((item) => item.selected == "true")[0];

  return <Pattern count={data?.count} imgUrl={data?.value} />;
}

function ShortDivisionCorrectAnswer({ data }) {
  const [choiceData, setChoiceData] = useState([]);
  const [correctdata, setCorrectdata] = useState([]);
  const getCorrectValue = (obj, temp) => {
    if (!Array.isArray(obj) && typeof obj === "object") {
      if (
        obj?.selected == "true" ||
        obj.isMissed == "true" ||
        obj?.option == "true"
      ) {
        temp.push({ ...obj });
        return true;
      } else return false;
    }
    if (Array.isArray(obj))
      for (let item of obj) {
        if (getCorrectValue(item, temp)) return;
      }
    return false;
  };
  console.log("data", data, choiceData);
  useEffect(() => {
    let arr = [];
    arr = data?.questionContent;
    setCorrectdata([...arr]);

    let temp = [];
    let questionContent = data?.questionContent || [];
    if (!Array.isArray(questionContent)) questionContent = data?.choices;
    getCorrectValue(questionContent, temp);
    setChoiceData([...temp]);
  }, []);
  console.log(correctdata, "correctdata");
  return correctdata?.length ? (
    <div className={styles.correctAnswerBox}>
      <div className={`${styles.correctAnswer} ${styles.correctAnswer2}`}>
        <h6>The correct answer is:</h6>
        <div style={{ position: "relative" }}>
          {correctdata?.map((items, index) => (
            <div
              key={index}
              style={index !== 2 ? itemWidth.divflex : itemWidth.divre_flex}
            >
              {index !== 2
                ? items?.map((item, i) => (
                    <div key={i} style={{ padding: "4px" }}>
                      {" "}
                      <p
                        style={itemWidth.item}
                        className={
                          item?.isMissed === "true"
                            ? `${styles.MissedValue}`
                            : ""
                        }
                      >
                        {item?.value}
                      </p>
                      {index == 0 && i == 1 && (
                        <>
                          <BottomBorder
                            width={i == 0 ? "0px" : "90%"}
                          ></BottomBorder>
                        </>
                      )}
                      {index == 1 && i == 0 && (
                        <>
                          <KeyRightPranthesis>)</KeyRightPranthesis>
                        </>
                      )}
                    </div>
                  ))
                : items.map((item, i) => (
                    <p
                      key={i}
                      style={itemWidth.re_item}
                      className={
                        item?.re_isMissed === "true"
                          ? `${styles.re_MissedValue}`
                          : ""
                      }
                    >
                      {" "}
                      {item?.re_value}
                    </p>
                  ))}
            </div>
          ))}
        </div>
      </div>
    </div>
  ) : (
    ""
  );
}

const itemWidth = {
  divflex: {
    display: "flex",
    gap: "15px",
    position: "relative",
  },
  divre_flex: {
    display: "flex",
    gap: "36px",
    position: "absolute",
    top: "35px",
    left: "38px",
  },
  item: {
    width: "25px",
    height: "25px",
    margin: "0",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  re_item: {
    width: "12px",
    height: "15px",
    margin: "1px 0px 0px 0px ",
    textAlign: "center",
    fontSize: "12px",
  },
};
export const KeyRightPranthesis = styled.div`
  position: absolute;
  top: 4px;
  transform: scale(1.8, 2.65);
  left: 30px;
  color: indigo;
`;
export const BottomBorder = styled.div`
  position: absolute;
  width: ${(props) => props.width ?? "100px"};
  bottom: -3px;
  height: 3px;
  right: -3px;
  background: indigo;
  margin-bottom: 2px;
`;
